<template>
    <div class="webshop-all">
      <div class="swiper-section">
          <div class="swipe-wrapper">
              <mt-swipe ref="swiperComponent" :auto="4000">
                  <mt-swipe-item v-for="site in topData">
                      <my-image class="my-img" :imgUrl="path+site.topicon" @click.native="clickDetailsHandle(site)"></my-image>
                  </mt-swipe-item>
              </mt-swipe>
          </div>
      </div>
      <div class="special-section">
        <div class="webshop-section-title">
          <div class="title-contaier"><span class="title">特价商品</span></div>
          <div class="line"></div>
        </div>
        <div class="webshop-circle-list special">
          <div class="circle-good" v-for="goodItem in specialData" @click="clickDetailsHandle(goodItem)">
            <my-image class="my-img" :imgUrl="path+goodItem.icon"></my-image>
            <p class="dec">限时特价</p>
            <p class="price"><span class="symbol">&yen;</span>{{goodItem.price}}</p>
          </div>
        </div>
      </div>
      <div class="integral-section"></div>
        <div class="webshop-section-title">
          <div class="title-contaier"><span class="title">积分商品</span></div>
          <div class="line"></div>
        </div>
        <div class="webshop-circle-list integral">
          <div class="circle-good" v-for="goodItem in integralData" @click="clickDetailsHandle(goodItem)">
            <my-image class="my-img" :imgUrl="path+goodItem.icon"></my-image>
            <p class="price single">{{parseInt(goodItem.price)}}<span class="integral">积分</span></p>
          </div>
        </div>
      <div class="advise-section">
        <div class="webshop-section-title">
          <div class="title-contaier"><span class="title">推荐商品</span></div>
          <div class="line"></div>
        </div>
        <ul class="webshop-rect-list all">
          <li class="webshop-rect-list-li" v-for="cellData in adviceData" @click="clickDetailsHandle(cellData)">
            <rect-good-item  :good-item="cellData"></rect-good-item>
          </li>
        </ul>
      </div>
      <div class="contact-section">
        <div class="webshop-section-title contact">
          <div class="title-contaier"><span class="title">联系我们</span></div>
          <div class="line"></div>
        </div>
      </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import { Swipe, SwipeItem } from "mint-ui";
import Vue from "vue";
import "mint-ui/lib/swipe/style.css";
import "mint-ui/lib/swipe-item/style.css";
import myImage from "../../lazyloadImg/lazyImage.vue";
import rectGoodItem from "./rectGoodItem.vue";

Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);

export default {
  name: "v-webshop-all",
  data() {
    return {
      topData: [],
      specialData: [],
      integralData: [],
      adviceData: [],
      isLoadDb: false,
      memoryScroll: 0
    };
  },
  components: { myImage, rectGoodItem },
  methods: {
    clickDetailsHandle(objs) {
      var mealItem = {};
      mealItem["cardId"] = objs.id + "";
      this.$store.commit("UPDATE_CURMEALDATA", mealItem);
      this.$router.push({ name: "details" });
    },
    LoadDb() {
      this.$http
        .post(this.$store.state.path + "php/Route.php", {
          type: "goodsbytp",
          gtype: -1,
          ranktp: 0,
          lift: 0,
          page: 0,
          userid: this.userid
        })
        .then(response => {
          if (response.body.error == "ok") {
            this.topData = response.body.arrtop;
            this.specialData = response.body.tearr;
            this.integralData = response.body.sourcearr;
            this.adviceData = response.body.tuiarr;
          }
          this.isLoadDb = true;
          this.$store.dispatch("toggleLoading", false);
        })
        .catch(response=>{
          this.$store.dispatch("toggleLoading", false);
        });
    }
  },
  activated() {
    document.body.scrollTop = this.memoryScroll;
    this.$refs.swiperComponent.initTimer();
    if (this.isLoadDb) this.$store.dispatch("toggleLoading", false);
  },
  mounted() {
    this.LoadDb();
  },
  beforeRouteLeave(to, from, next) {
    this.memoryScroll = document.body.scrollTop;
    this.$refs.swiperComponent.clearTimer();
    next();
  },
  computed: {
    ...mapState(["path", "icon", "userid"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.webshop-all {
  padding-bottom: 0.58rem;

  .webshop-rect-list {
    flex-wrap: wrap;
    display: flex;
    justify-content: space-between;
    margin: 0 0.15rem;
    font-size: 0;

    &.all {
      margin-top: 0.05rem;
    }

    .webshop-rect-list-li {
      margin-top: 0.08rem;
      flex: 0 1 1.4rem;
      background-color: #fffded;
    }
  }

  .webshop-section-title {
    height: 0.2rem;
    margin: 0.1rem 0.2rem 0 0.2rem;
    text-align: center;
    position: relative;

    &.contact {
      margin: 0.15rem 0.2rem 0.15rem 0.2rem;
    }

    .title-contaier {
      background: #fff798;
      display: inline-block;
      height: 100%;
      width: 0.9rem;
      border-radius: 0.2rem;

      .title {
        font-weight: 500;
        display: inline-block;
        padding-top: 0.04rem;
        font-size: 0.13rem;
        color: #a2733d;
        letter-spacing: 0.5px;
      }
    }

    .line {
      position: absolute;
      top: 50%;
      height: 1px;
      background-color: #fff798;
      width: 100%;
      z-index: -1;
    }
  }

  // 商城圆形商品样式
  .webshop-circle-list {
    display: flex;
    justify-content: space-between;
    margin: 0.15rem 0.2rem;

    &.special {
      min-height: 1.2rem;
    }

    &.integral {
      min-height: 1.1rem;
    }

    .circle-good {
      flex: 0 1 0.8rem;
      text-align: center;

      .my-img {
        display: inline-block;
        width: 0.8rem;
        height: 0.8rem;

        img {
          border-radius: 50%;
        }
      }

      .dec {
        margin-top: 0.1rem;
        font-size: 0.12rem;
        color: #956134;
        letter-spacing: 0.01rem;
      }

      .price {
        margin-top: 0.04rem;
        color: #e60012;
        font-size: 0.12rem;

        .symbol {
          margin-right: 0.03rem;
        }

        &.single {
          margin-top: 0.14rem;
        }

        .integral {
          color: #956134;
          margin-left: 0.03rem;
          font-size: 0.13rem;
        }
      }
    }
  }
}
</style>



